.MainElectricMotors {
  background-color: #444444;
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;

  height: 100vh;
  position: relative; 

}
.MainElectricMotors__title {
  position: absolute;
  left: 2%;
  top: 2%;
  width: 10%;
  color: #ffdd93;
  font-size: 5vw;
  font-weight: 700;
}
.MainElectricMotors__module {
  position: absolute;
  left: 3%;
  top: 10%;
  width: 50%;
  height: 80%;
  border-radius: 20%;
}
.module__pic {
  position: absolute;
  left: 3%;
  top: 3%;
  width: 100%;
  height: 100%;
  opacity: 80%;
  border-radius: 20%;
}
.drag {
  position: absolute;
  left: 63%;
  top: 10%;
  width: 9%;
  height: 25%;
  text-align: center;
  color: #ffdd93;
  font-weight: 700;
  font-size: 1vw;
}
.drag__magnet {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: red;
}
.magnet__spolar {
  position: absolute;
  width: 100%;
  height: 50%;
  background-color: blue;
  color: #ffdd93;
}
.magnet__handler {
  position: absolute;
  top: 37.5%;
  left: 0%;
  width: 100%;
  height: 25%;
  background-color: black;
  color: #ffdd93;
  cursor: move;
}

.module__touch-line {
  position: absolute;
  left: 61%;
  top: 53%;
  width: 13%;
  height: 2%;
  background-color: transparent;
}

.module__lightBulb-off {
  position: absolute;
  left: 13%;
  top: 52%;
  width: 12%;
  height: 12%;
  opacity: 0;
  border-radius: 70%;
}
.module__lightBulb-on {
  position: absolute;
  left: 13%;
  top: 52%;
  width: 12%;
  height: 12%;
  border-radius: 70%;
  opacity: 1;
  background-color: rgb(255, 251, 0);
  animation-name: flickering;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}
@keyframes flickering {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.MainElectricMotors__text {
  position: absolute;
  left: 60%;
  top: 20%;
  color: #ffdd93;
  font-size: 1.9vw;
  font-weight: 700;
}

.MainElectricMotors__link {
  position: absolute;
  left: 60%;
  top: 50%;
  font-size: 1.9vw;
  font-weight: 700;
  cursor: pointer;
}
.MainElectricMotors__link-text {
  color: #ffdd93;
  cursor: pointer;
}

.slick{
  position:absolute;  
  width: 50%;
  height: 50%;  
}
.slide-test{
  position:absolute;  
  width: 50%;
  height: 50%;  
}