.MainRefrigerators {
  background-color: black;
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  height: 100vh;
  position: relative;
}
.MainRefrigerators__background {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 30%;
}
.MainRefrigerators__title {
  position: absolute;
  left: 2%;
  top: 2%;
  height: 10%;
  color: #ffdd93;
  font-size: 5vw;
  font-weight: 700;
}

.MainRefrigerators__body {
  position: absolute;
  height: 90%;
  width: 100%;
}

.MainRefrigerator__guide {
  position: absolute;
  left: 60%;
  top: 12%;
  font-size: 2vw;
  font-weight: 700;
  color: #ffdd93;
}
.guide__text {
  color: #ffdd93;
  cursor: pointer;
}

.MainRefrigerators__grid {
  position: absolute;
  left: 2%;
  top: 15%;
  display: grid;
  /* 1fr은 남은 자유공간의 크기를 할당하는 것을 의미한다. */
  /* 1fr 1fr 1fr 1fr 이라고 쓰면 총 4fr 중 각각을 1fr 씩 나누므로, 각각은 1/4fr이 된다. */
  /* grid-auto 옵션은 columns나 rows의 갯수를 모를때 지정해준다. */
  width: 50%;
  height: 90%;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
}

.MainRefrigerators__grid__item:nth-child(1) {
  position: absolute;
  width: 100%;
  height: 100%;
  grid-column: 1 / 3;
  grid-row: 1 / 2;
  border: black solid 1px;
}
.item1__indoor {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #333;
}
.item1__indoor_img-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #ffdd93;
  color: rgb(65, 65, 65);
  font-size: 1vw;
  font-weight: 700;
}
.item1__indoor__img {
  position: absolute;
  width: 100%;
  height: 100%;
}
.item1__indoor__text {
  position: absolute;
  width: 100%;
  height: 100%;
}
.item1__outdoor {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #d0d1c9;
  /* transform-origin은 해당 엘리먼트에 차후
  transform이 일어났을 때 기준점을 설정해줌 */
  transform-origin: right;
  transition: all 1s ease-in-out;
  /* perspective는 내가 애니메이션을 보는 높이를 의미함 */
  transform: perspective(5000px) translateZ(0px) translateX(0px) translateY(0px) rotateY(0deg) rotateZ(0deg);
}
.doorOpen1 {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 80%;
  background-color: #d0d1c9;
  transform-origin: right;
  transition: all 1s ease-in-out;
  /* perspective는 내가 애니메이션을 보는 높이를 의미함 */
  transform: perspective(5000px) translateZ(0px) translateX(0px) translateY(0px) rotateY(85deg) rotateZ(0deg);
}
.outDoor1__text {
  position: absolute;
  left: 15%;
  top: 25%;
  color: rgb(65, 65, 65);
  font-size: 2vw;
  font-weight: 700;
  text-align: center;
}

