  padding-top: 100px;
}

.MainPages__welcomePoint {
  background-image: url(/static/media/Museum.5e9d3817.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  position: relative;
  width: 100%;
  height: 100%;
}

.MainPages__welcomePointText {
  color: #000000;
  font-size: 3.5vmax;
  font-weight: 900;
  position: absolute;
  width: 100%;
  text-align: center;
  margin-top: 11%;
  text-shadow: 0px 0px 20px white;
  animation: appearTitle 3s cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
  font-family: "Cormorant Garamond", serif;
  letter-spacing: 0.1em;
}

.MainPages__welcomePointSubText {
  color: #000000;
  font-size: 1.7vmax;
  font-weight: 500;
  position: absolute;
  width: 100%;
  text-align: center;
  margin-top: 17%;
  margin-left: 0.8%;
  text-shadow: 0px 0px 20px white;
  letter-spacing: 0.1em;
  font-family: "Cormorant Garamond", serif;
  animation: appearTitle 3s cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
}

@keyframes appearTitle {
  0% {
    letter-spacing: 0.7em;
    transform: translateZ(300px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}

@keyframes appearTitleM {
  0% {
    transform: translateZ(300px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}

