  font-size: 2vw;
  font-weight: 700;
  color: #ffdd93;
  opacity: 0;
  transform: rotate(-25deg);
  transition: all 2s ease-in;
  animation-name: img4-rocket-link;
  animation-delay: 12s;
  animation-duration: 3s;
  animation-fill-mode: forwards;
}

@keyframes img4-rocket-link {
  from {
    opacity: 0;
    z-index: -1;
  }
  to {
    opacity: 1;
    z-index: 0;
  }
}

