* {
  box-sizing: border-box;
}

.MainPages {
  height: 100%;
  width: 100%;
  display: block;
  padding-top: 100px;
}

.MainPages__welcomePoint {
  background-image: url("../images/MainPage/Museum.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  position: relative;
  width: 100%;
  height: 100%;
}

.MainPages__welcomePointText {
  color: #000000;
  font-size: 3.5vmax;
  font-weight: 900;
  position: absolute;
  width: 100%;
  text-align: center;
  margin-top: 11%;
  text-shadow: 0px 0px 20px white;
  animation: appearTitle 3s cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
  font-family: "Cormorant Garamond", serif;
  letter-spacing: 0.1em;
}

.MainPages__welcomePointSubText {
  color: #000000;
  font-size: 1.7vmax;
  font-weight: 500;
  position: absolute;
  width: 100%;
  text-align: center;
  margin-top: 17%;
  margin-left: 0.8%;
  text-shadow: 0px 0px 20px white;
  letter-spacing: 0.1em;
  font-family: "Cormorant Garamond", serif;
  animation: appearTitle 3s cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
}

@keyframes appearTitle {
  0% {
    letter-spacing: 0.7em;
    -webkit-transform: translateZ(300px);
    transform: translateZ(300px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    opacity: 1;
  }
}

@keyframes appearTitleM {
  0% {
    -webkit-transform: translateZ(300px);
    transform: translateZ(300px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    opacity: 1;
  }
}

.scroll__tracker {
  border: 1px solid red;
  width: 600px;
  height: 100px;
  overflow: scroll;
}

.LinkedDetail {
  color: white;
  font-size: 50px;
  font-weight: 700;
  position: absolute;
  width: 100%;
  text-align: center;
  margin-top: 430px;
}

.MainPages__ScrollSign {
  position: absolute;
  color: rgb(78, 76, 76);
  bottom: 11%;
  font-size: 2.3vmax;
  font-weight: 700;
  left: 45.6%;
  text-shadow: 0px 0px 20px white;
  font-family: "Cormorant Garamond", serif;
  background-color: rgba(255, 255, 247, 0.479);
  border-radius: 5px;
  width: 9%;
  height: 6.2%;
  text-align: center;
}

@media screen and (max-width: 385px) {
  .MainPages__welcomePointText {
    top: 13%;
    animation: appearTitleM 3s cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
  }

  .MainPages__welcomePointSubText {
    top: 20%;
    animation: appearTitleM 3s cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
  }

  .MainPages__ScrollSign {
    width: 15%;
    height: 4%;
    margin-left: -11px;
    bottom: 27%;
  }
}
