
.MainLightBulbs {  
  height: 100vh;
  position: relative;     

}

.MainLightBulbs__body {
  position: absolute;
  width: 100%;
  height: 100%;
}
.body__title {
  position: absolute;
  width: 100%;
  height: 10%;
  top: 6%;
  left: 10%;
  z-index: 1;
  color: #ffdd93;
  font-size: 5vw;
}
.title__switchBtn {
  position: absolute;
  width: 9%;
  height: 180%;
  left: -9%;
  top: -70%;
  cursor: pointer;
}
.title__link {
  position: absolute;
  top: 20%;
  left: 25%;
  color: #ffdd93;
  font-size: 3vw;
}
.title_icon-guide {
  position: absolute;
  top: 100%;
  left: -8%;
  color: red;
  font-size: 2vw;
}

.body__background {
  position: absolute;
  width: 100%;
  height: 100%;
}
.background__texts-off {
  position: absolute;
  width: 100%;
  height: 100%;
  color: #ffdd93;
  animation-name: blink-off;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
@keyframes blink-off {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.2;
  }
}
.background__texts-on {
  position: absolute;
  width: 100%;
  height: 100%;
  color: #ffdd93;
  opacity: 1;
  transition: all 1s ease-in-out;
}

.text1 {
  position: absolute;
  width: 25%;
  top: 30%;
  left: 10%;
  color: #ffdd93;
  font-size: 2vw;
}
.text2 {
  position: absolute;
  width: 25%;
  top: 65%;
  left: 15%;
  color: #ffdd93;
  font-size: 2vw;
}
.text3-img {
  position: absolute;
  width: 25%;
  height: 25%;
  top: 25%;
  left: 70%;
  opacity: 0.7;
  border-radius: 10%;
  color: #ffdd93;
}
.text3 {
  position: absolute;
  width: 35%;
  height: 25%;
  top: 55%;
  left: 68%;
  border-radius: 10%;
  color: #ffdd93;
  font-size: 2vw;
}
